












































































import Vue from 'vue';
export default Vue.extend({
  data (): Record<string, any> {
    return {
      valid: false,
      nickname: '',
      email: '',
      address: '',
      rules: {
        required: (v: string) => !!v || '  .',
        nickname: (v: string) => (v.length >= 2 && v.length <= 10) || ' 2~10 .',
        email: (v: string) => /.+@.+/.test(v) || '   .',
        // address: 
      }
    }
  },
  methods: {
    submit () {
      console.log('submit')
    },
    jusoPopup () {
      console.log('juso popup')
      // const pop = window.open("./juso.jsp","pop","width=570,height=420, scrollbars=yes, resizable=yes");
      window.open("./juso.jsp","pop","width=570,height=420, scrollbars=yes, resizable=yes");
    },
    goToMain () {
      this.$router.push('/')
    }
  }
})
